.Experience {
  color: whitesmoke;
  align-items: center;
  text-align: center;
  margin-top: 5%;

  main {
    min-width: 40%;
    max-width: 50%;
    margin: auto;
    box-shadow: 4px 4px 90px black;
  }

  p {
    font-size: 0.8em;
    line-height: 1.75em;
    border-top: 3px solid;
    border-image: linear-gradient(to right, #743ad5 0%, #d53a9d 100%);
    border-image-slice: 1;
    border-width: 3px;
    margin: 0;
    padding: 40px;
    counter-increment: section;
    position: relative;
    color: white;

    //numbers
    &:before {
      content: counter(section);
      position: absolute;
      border-radius: 50%;
      padding: 10px;
      height: 1.25em;
      width: 1.25em;
      background-color: black;
      text-align: center;
      line-height: 1.25em;
      color: white;
      font-size: 1em;
    }
  }
  //odd number borders
  p:nth-child(odd) {
    border-right: 3px solid;
    padding-left: 0;

    &:before {
      left: 100%;
      margin-left: -20px;
    }
  }
  //even number borders
  p:nth-child(even) {
    border-left: 3px solid;
    padding-right: 0;

    &:before {
      right: 100%;
      margin-right: -20px;
    }
  }
  //handle first and last
  p:first-child {
    border-top: 0;
    border-top-right-radius: 0;
    border-top-left-radius: 0;
  }
  p:last-child {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
}

@media only screen and (max-width: 786px) {
  .Experience {
    main {
      max-width: 90%;
      h4 {
        font-size: 1rem;
      }
      h5 {
        font-size: 0.8rem;
      }
    }

    p {
      padding: 10px;
      font-size: 0.6em;
    }
  }
}
